.nav{
    position: absolute;
    display: flex; 
    align-items: center; 
    justify-content: space-between;
    width: calc(100% - 80px);
    padding: 32px 40px 0px 40px;
    z-index: 9999;
    transform: translate(0px, -200px);
    @include mobileMax952p{
        padding: 32px 32px 0px 32px;
    }
    &__logo{
        width: 18px;
        img{
            width: 177px;
            height: 18px;
            @include windowMax1800p {
                width: 141px;
                height: 14px; 
            }
            @include mobileMax952p{
                width: 141px;
                height: 14px; 
            }
        }
    }
    &__burger{
        @include mobileMax952p{
            cursor: pointer;
            &::before{
                content: "";
                height: 2px;
                background-color: white;
                width: 18px;
                display: block;
                transition: 0.3s ease-in-out;
                transform-origin: 5px;
            }
            &::after{
                content: "";
                height: 2px;
                background-color: white;
                width: 18px;
                margin-top: 4px;
                display: block;
                transition: 0.3s ease-in-out;
                transform-origin: 5px;
            }
        }
    }
    &__burger-active{
        &::before{
            rotate: 45deg;
        }
        &::after{
            rotate: -45deg;
        }
    }
    &__menu-active{
        @include mobileMax952p{
            top: 176px !important;
            // transition-delay: 0.4s;
            .nav__menu__burger {
                // transition-delay: 0s;
            }
        }
    }
    &__menu{
        display: flex;
        align-items: center;
        gap: 48px;
        @include mobileMax952p{
            position: absolute;
            margin-top: -176px;
            padding-top: 176px;
            left: 0px;
            display: flex;
            flex-direction: column;
            gap: 80px;
            top: -90vh;
            background-color: #18183C;
            z-index: -1;
            padding-bottom: calc(100vh - 176px - 240px);
            width: 100vw;
            transition: 0.75s ease-out;
        }

        &__item-active{
            &::after{
                width: 100% !important;
            }
        }
        &__burger{
            @include mobileMax952p{
                transition: 0.4s ease-out;
                transition-delay: 0.85s;
                opacity: 0;
            }
        }
        &__burger-active{
            @include mobileMax952p{
                opacity: 1;
            }
        }
        &__item{
            @include poppins14_500();
            color: var(--white-color);
            text-align: center;
            letter-spacing: 0.08em;
            font-feature-settings: "liga" off;
            text-transform: uppercase;
            cursor: pointer;
            @include windowMax1800p {
                @include poppins12_500();
                letter-spacing: 0.1em;
            }
            @include windowMax1280p{
                @include poppins10_500();
            }
            &::after{
                content: "";
                height: 2px;
                background-color: white;
                width: 0px;
                display: inherit;
                transition: width 0.3s ease-out;
            }
            @media (hover: hover) {
                &:hover{
                    &::after{
                        width: 100%;
                    }
                }
            }
        }
        &__button{
            background-color: var(--iris-color);
            border-radius: 600px;
            cursor: pointer;
            transition: background-color 0.3s ease-out;
            @include mobileMax952p{
                background-color: transparent;
                &::after{
                    content: "";
                    height: 2px;
                    background-color: white;
                    width: 0px;
                    display: inherit;
                    transition: width 0.3s ease-out;
                }
                @media (hover: hover) {
                    &:hover{
                        background-color: transparent;
                        &::after{
                            width: 100%;
                        }
                    }
                }
            }
            p{
                padding: 14px 32px 14px 32px;
                @include poppins14_500();
                color: var(--white-color);
                text-transform: uppercase;
                @include windowMax1800p {
                    padding: 12px 28px 12px 28px;
                    @include poppins12_500();
                    letter-spacing: 0.08em;
                }
                @include windowMax1280p{
                    @include poppins10_500();
                    letter-spacing: 0.1em;
                }
                @include mobileMax952p{
                    padding: 0;
                }
            }
            @media (hover: hover) {
                &:hover{
                    background-color: var(--iris-color-hover);
                }
            }
        }
    }
}