.contact{
    min-height: calc(100vh - 120px);
    width: 100vw;
    overflow: visible;
    padding-top: 100px;
    padding-bottom: 20px;

    .container{
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 100%;
        min-height: calc(100vh - 120px);
        opacity: 0;
        transform: translateY(400px);
        @include mobileMax952p{
            //padding-top: 120px;
            flex-direction: column ;
        }
    }

    &__content{
        display: flex;
        flex-direction: column;
        //margin-top: 120px;
        //margin-bottom: 120px;
        gap: 48px;
        @include windowMax1800p{
            gap: 28px;
        }
        @include windowMax1280p{
            gap: 24px;
        }
        @include mobileMax952p{
            gap: 40px;
            margin-top: 0px;
        }
        h1{
            @include poppins56();
            letter-spacing: 0.02em;
            color:rgba($color: #ffffff, $alpha: 0.2);
            @include windowMax1800p {
                @include poppins40();
                max-width: 569px;
            }
            @include windowMax1280p{
                @include poppins32();
            }
            @include mobileMax952p{
                @include poppins32();
            }
        }

        &__textBlock{
            display: flex;
            flex-direction: column;
            gap: 48px;
            @include windowMax1800p{
                gap: 24px;
            }
            @include windowMax1280p{
                gap: 20px;
            }
            @include mobileMax952p{
                gap: 32px;
            }
            &__item{
                &__headline{
                    display: flex;
                    align-items: center;
                    gap: 16px;
                    padding-bottom: 16px;
                    @include windowMax1800p{
                        padding-bottom: 12px;
                    }
                    @include windowMax1280p{
                        padding-bottom: 8px;
                    }
                    @include mobileMax952p{
                        padding-bottom: 12px;
                    }
                    &__circleIcon{
                        width: 40px;
                        height: 40px;
                        border-radius: 100%;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        border: 0.75px solid rgba($color: #ffffff, $alpha: 0.3);
                        @include windowMax1280p{
                            width: 32px;
                            height: 32px;
                        }
                        @include mobileMax952p{
                            width: 32px;
                            height: 32px;
                        }
                    }
                    h2{
                        @include poppins16_500();
                        line-height: 32px;
                        color: var(--white-color);
                        @include windowMax1800p {
                            @include poppins14_500();
                        }
                        @include windowMax1280p{
                            @include poppins12_500();
                        }
                        @include mobileMax952p{
                            @include poppins14_500();
                        }
                    }
                }
                p, a{
                    @include poppins16();
                    letter-spacing: 0.02em;
                    color: var(--white-color);
                    opacity: 0.7;
                    line-height: 34px;
                    
                    @include windowMax1800p {
                        @include poppins14();
                    }
                    @include windowMax1280p{
                        @include poppins12();
                    }
                    @include mobileMax952p{
                        @include poppins14();
                    }
                }
                &__links{
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                    //gap: 16px;
                    @include mobileMax952p{
                        flex-direction: column;
                        align-items: flex-start;
                        &__circle {
                            display: none;
                        }
                    }
                    &__circle{
                        background: #FFFFFF;
                        opacity: 0.7;
                        width: 4px;
                        height: 4px;
                        border-radius: 100%;
                    }
                    p{
                        padding-top: 16px;
                        @include poppins14();
                        letter-spacing: 0.02em;
                        color: #FFFFFF;
                        opacity: 0.3;
                        @include windowMax1800p {
                            padding-top: 12px;
                            @include poppins14();
                        }
                        @include windowMax1280p{
                            padding-top: 8px;
                            @include poppins12();
                        }
                        @include mobileMax952p{
                            @include poppins14();
                        }
                    }
                }
            }
        }
    }

    &__map{
        border: 2px solid #5454D6;
        border-radius: 32px;
        width: 672px;
        height: 672px;
        padding: 18px;
        box-sizing: border-box;
        display: flex;
        justify-content: center;
        align-items: center;
        position: relative;
        overflow: hidden;
        //margin-top: 120px;
        //margin-bottom: 120px;
        @include windowMax1800p{
            padding: 14px;
            width: 509px;
            height: 509px;              
        }
        @include windowMax1280p{
            padding: 12px;
            width: 456px;
            height: 456px;
        }
        
        @include mobileMax952p{
            border-radius: 24px;
            padding: 8px;
            margin-top: 58px;
            margin-bottom: 88px;
            width: calc(100% - 16px);
            height: min(calc(100vw - 64px), 536px);
        }
        &__frame{
            position: relative;
            border-radius: 24px;
            // border: 5px solid red;
            // box-sizing: border-box;
            overflow: hidden;
            height: 100%;
            width: 100%;
            .gm-style > div {
                overflow: hidden;
                border-radius: 24px;
                @include mobileMax952p{
                    border-radius: 16px;
                }
            }
            @include windowMax1800p{

            }
            @include windowMax1280p{

            }
            @include mobileMax952p{
                border-radius: 16px;
            }
        }
        .white-box{
            position: absolute;
            width: calc(100% - 48px);
            height: calc(100% - 48px);
            background-color: #f9f9f9;
            border-radius: 24px;
            z-index: -1;
        }
    }
}