.hero__bg{
    position: absolute;
    width: 100%;
    height: calc(170vh);
    overflow: hidden;
    z-index: -1;

    img{
        display: block;
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: center top;
        @include mobileMax952p{
            object-position: 60% top;
        }
    }
}
.hero__shape{
    position: absolute;
    width: 100%;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-shrink: 0;
    top: 0px;
    z-index: 3;
    @include mobileMax952p {
        overflow: hidden;
    }
    svg{
        //border: 2px solid red;
        //translate: calc(100vw / 2 - 313px) calc(100vh / 2 - 524px);
        width: 800px;
        height: 800px;
        // width: 1000px;
        // height: 1000px;
        // scale: 0.9;
        flex-shrink: 0;
        scale: 0;
        overflow: visible;
        @include windowMax1800p{
            // transform: scale(0.7);
            width: 650px;
            height: 650px;
        }
        @include windowMax1280p{
            width: 600px;
            height: 600px;
        }
        @include mobileMax952p{
            width: 500px;
            height: 500px;
        }
    }
}
.hero{
    width: 100vw;
    height: 100vh;
    position: relative;
    overflow: hidden;
    &__content{
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        overflow: hidden;
        &__circle{
            width: calc(100vh - 120px);
            height: calc(100vh - 120px);
            min-width: 620px;
            min-height: 620px;
            margin: 60px 0px 60px 0px;
            flex-shrink: 0;
            border-radius: 100%;
            background: #05051bf1;
            display: flex;
            justify-content: center;
            align-items: center;
            scale: 0;
            @include mobileMax952p{
                width: calc(100vh - 252px);
                height: calc(100vh - 252px);
                min-width: 502px;
                min-height: 502px;
                margin: 126px 0px 126px 0px;
            }
            &__ring{
                margin: 9.375%;
                height: calc(100% - 2 * 9.375%);
                width: calc(100% - 2 * 9.375%);
                flex-shrink: 0;
                border-radius: 100%;
                border: 2px solid var(--iris-color);
                display: flex;
                justify-content: center;
                align-items: center;
                scale: 0.7;
                &__text{
                    z-index: 3;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    flex-direction: column;
                    
                    @include mobileMax952p{
                        max-width: 301px;
                    }
                    h1{
                        @include poppins56();
                        //max-width: 634px;
                        font-size: 5.186vh;
                        line-height: 6.668vh;
                        text-align: center;
                        color: var(--white-color);
                        padding-bottom: 40px;
                        letter-spacing: 0.02em;
                        @include windowMax1800p {
                            //@include poppins40();
                            font-size: 5vh;
                            line-height: 7vh;
                        }
                        @include windowMax1280p{
                            //@include poppins32();
                            font-size: 4.44444vh;
                            line-height: 6.66666vh;
                        }
                        @include mobileMax952p{
                            //@include poppins32();
                            font-size: 3.7vh;
                            line-height: 5.9vh;
                            padding-bottom: 28px;
                            br{
                                display: none;
                            }
                        }
                    }
                    &__action{
                        display: flex;
                        gap: 16px;
                        cursor: pointer;
                        p{
                            @include poppins14_500();
                            color: var(--white-color); 
                            letter-spacing: 0.1em;
                            font-feature-settings: 'liga' off;
                            text-transform: uppercase;
                            @include windowMax1800p {
                                @include poppins12_500();
                            }
                            @include windowMax1280p{
                                @include poppins10_500();
                            }
                            @include mobileMax952p{
                                @include poppins12_500();
                            }
                            /*
                            &::after{
                                content: "";
                                height: 2px;
                                background-color: white;
                                width: 100%;
                                left:50%;
                                display: inherit;
                                transition: transform 0.3s ease-out;
                                transform: scaleX(0);
                            }*/
                        }
                        &__button{
                            background-color: var(--iris-color);
                            transition: background-color 0.3s ease-out;
                            width: 28px;
                            height: 28px;
                            border-radius: 60px;
                            display: flex;
                            justify-content: center;
                            align-items: center;
                            @include windowMax1280p{
                                width: 24px;
                                height: 24px;
                            }
                        }
                        @media (hover: hover) {
                            &:hover &__button{
                                background-color: var(--iris-color-hover);
                            }
                        }
                        /*
                        &:hover p{
                            &::after{
                                transform: scaleX(1);
                            }
                        }*/
                    }
                }
            }
        }
    }
}