.aboutus{
    width: 100vw;
    overflow: hidden;
    @include mobileMax952p{
        padding-bottom: 80px;
    }
    .container{
        display: flex;
        flex-direction: column;
        align-items: center;
        transform: translateY(400px);
        opacity: 0;
    }
    &__text{
        padding-top: 208px;
        display: flex;
        flex-direction: column;
        align-items: center;
        @include windowMax1800p {
            padding-top: 176px;
        }
        @include windowMax1280p{
            padding-top: 160px;
        }
        @include mobileMax952p{
            padding-top: 136px;
        }
        h1{
            @include poppins56();
            letter-spacing: 0.02em;
            color: rgba($color: #ffffff, $alpha: 0.2);
            text-align: center;
            @include windowMax1800p {
                @include poppins40();
                max-width: 569px;
            }
            @include windowMax1280p{
                @include poppins32();
            }
            @include mobileMax952p{
                @include poppins28();
            }
        }
        p{    
            @include poppins16_300();
            line-height: 32px;
            color: rgba($color: #ffffff, $alpha: 0.8);
            letter-spacing: 0.04em;
            text-align: center;
            max-width: 828px;
            padding-top: 48px;
            @include windowMax1800p {
                @include poppins14_300();
                padding-top: 32px;
            }
            @include windowMax1280p{
                @include poppins12_300();
                padding-top: 24px;
            }
            @include mobileMax952p{
                @include poppins14_300();
                padding-top: 24px;
            }
        }
    }
    &__image{
        margin-top: 112px;
        width: 1223px;
        overflow: hidden;
        display: flex;
        align-items: center;
        border-radius: 16px;
        img{
            width: 100%;
        }
        @include windowMax1800p {
            margin-top: 112px;
            width: 1100px;
        }
        @include windowMax1280p{
            margin-top: 112px;
            width: 960px;
        }
        @include mobileMax952p{
            margin-top: 112px;
            box-sizing: border-box;
            width: calc(100% - 16px);
            height: 383px;
            img{
                width: auto;
                height: 100%;
                transform: translateX(-14%);
            }
        }
    }
    &__divider{
        padding-top: 160px;
        display: flex;
        align-items: center;
        gap: 48px;
        @include windowMax1800p {
            padding-top: 132px;
        }
        @include windowMax1280p{
            padding-top: 120px;
        }
        @include mobileMax952p{
            padding-top: 80px;
        }
        p{
            @include poppins16_300();
            line-height: 32px;
            letter-spacing: 0.1em;
            color: #FFFFFF;
            opacity: 0.4;
            width: 181px;
            @include windowMax1800p {
                @include poppins14_300();
                width: 159px;
            }
            @include windowMax1280p{
                @include poppins12_300();
                width: 136px;
            }
            @include mobileMax952p{
                @include poppins12_300();
                width: 136px;
            }
        }
        &__line{
            opacity: 0.16;
            background-color: #FFFFFF;
            height: 1px;
            width: 50vw;
        }
    }
    &__bottomtext{
        display: flex;
        flex-direction: column;
        align-items: center;
        padding-top: 80px;
        gap: 16px;
        max-width: 911px;
        @include windowMax1800p {
            padding-top: 72px;
        }
        @include windowMax1280p{
            padding-top: 64px;
        }
        @include mobileMax952p{
            padding-top: 40px;
        }
        h1{
            @include poppins18_500();
            text-align: center;
            letter-spacing: 0.04em;
            color: #FFFFFF;
            @include windowMax1800p {
                @include poppins16_500();
            }
            @include windowMax1280p{
                @include poppins14_500();
            }
            @include mobileMax952p{
                @include poppins16_500();
            }
        }
        p{
            @include poppins16_300();
            text-align: center;
            letter-spacing: 0.04em;
            color: rgba(255, 255, 255, 0.8);
            @include windowMax1800p {
                @include poppins14_300();
            }
            @include windowMax1280p{
                @include poppins12_300();
            }
            @include mobileMax952p{
                @include poppins14_300();
            }
        }
    }
}